#drivers {
    min-height: 100vh;
}

#drivers h5 {
    margin-top: 2rem;
}

.drivers-list {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: .6rem;
    margin-top: 3.5rem;
    padding-bottom: 3.5rem;
}

.driver-item {
    background: var(--color-bg-light);
    width: 90%;
    height: 4.5rem;
    padding: .7rem 0;
    border-radius: 1.2rem;
    color: var(--white);
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    gap: .8rem;
}

.driver-item:focus {
    background: transparent;
}

.driver-item-image{
    /* background: red; */
    border-radius: .7rem;
    width: 3.2rem;
    height: 3.2rem;
    margin-left: 1rem;
}

.driver-item-image img {
    border-radius: .7rem

}

.driver-item-infos h6 {
    font-weight: 100;
}

/* ----- Media Queries ----- */

@media screen and (min-width: 1024px) {
    
    .drivers-list {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: center;
        gap: 1rem;
    }
    
    .driver-item {
        width: 22%;
    }
}